    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
}

/* Added above as original webpage was larger than content displayed*/

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}


.header {
    background-color: #fff;
    box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);
    width: 100%;
    z-index: 3;
  }

  .header ul {
    margin: 15px;
    font-size: 25px;
    padding: 0;
    list-style: none;
    overflow: hidden;
    background-color: #fff;
  }
  .form {
    max-width: 300px;
    background-color: #9b9b9b;
    margin-left: 37%;
    margin-top: 2%;

  }
  .header li a {
    display: block;
    padding: 20px 20px;
    opacity: 80%;
    border-right: 1px solid #f4f4f4;
    text-decoration: none;
  }
  
  .header li a:hover {
    background-color: #f4f4f4;
  }
  
  .header .logo {
    display: block;
    float: left;
    font-size: 40px;
    margin: 15px;
    opacity: 80%;
    padding: 10px 20px;
    text-decoration: none;
  }

.menubtn {
    background-color: #f4f4f4;
    opacity: 80%;
  }
  
  .logo {
    background-color: #f4f4f4;
  }
  .header .menu {
    clear: both;
    opacity: 80%;
    max-height: 0;
    transition: max-height .2s ease-out;
  }

  .header .menu-icon {
    cursor: pointer;
    display: inline-block;
    background-color: #f4f4f4;
    float: right;
    margin: 14px;
    padding: 28px 20px;
    position: relative;
    user-select: none;
  }
  
  .header .menu-icon .navicon {
    background: #333;
    display: block;
    height: 2px;
    position: relative;
    transition: background .2s ease-out;
    width: 18px;
  }
  
  .header .menu-icon .navicon:before,
  .header .menu-icon .navicon:after {
    background: #333;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all .2s ease-out;
    width: 100%;
  }
  
  .header .menu-icon .navicon:before {
    top: 5px;
  }
  
  .header .menu-icon .navicon:after {
    top: -5px;
    
  }
  .header .menu-btn {
    display: none;
  }
  
  .header .menu-btn:checked ~ .menu {
    max-height: 240px;
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon {
    background: transparent;
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon:before {
    transform: rotate(-45deg);
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon:after {
    transform: rotate(45deg);
  }
  
  .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
  .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
    top: 0;
  }
  @media (min-width: 48em) {
    .header li {
      float: left;
    }
    .header li a {
      padding: 20px 30px;
    }
    .header .menu {
      clear: none;
      float: right;
      max-height: none;
      background-color: #f4f4f4;
    }
    .header .menu-icon {
      display: none;
      background-color: #f4f4f4;
    }
  }

